type OptionType = any

@Component({
  components: {
    Pie,
  }
})
class PieParse extends Vue {

  static options: OptionType = options

  render (h, section, children) {
    const options = handleOptionsData(section.section.option.options)
    const option = {
      tooltip: {
        trigger: 'item'
      },
      legend: {
        // orient: 'horizontal',
        top: '35%',
        left: '70%',
        icon: "circle",
        itemWidth: 10,
        itemHeight: 10,
        itemGap: 10,
        width: 10,
        textStyle: {
          color: '#fff',
          fontSize: 12
        },
      },
      series: [
        {
          name: '',
          type: 'pie',
          center: ['40%', '50%'],
          radius: ['30%', '90%'],
          color: ['#ffc148', '#4992ff', '#0CF9FF'],
          data: [
            {
              value: 10, 
              name: ''
            },
            {
              value: 5,
              name: ''
            },
            {
              value: 10,
              name: ''
            },
          ],
          itemStyle: {
            color: '#ff5a5a',
            normal: {
              label: {
                textStyle: {
                  color: '#000',
                }
              }
            },
          },
          label: {
            show: true,
            color: '#fff',
            normal: {
              formatter: '{c}',
              position:'inside'
            },
            itemStyle: {
              color: '#ff5a5a'
            }
          },
        }
      ]
    }

    const _propsOn = {
      nativeOn: {
        click: e => {
          e.stopPropagation()
          store.dispatch('biCharts/setSelectedType', {
            selectedType: 'PieParse'
          })
          store.dispatch('biCharts/setUuid', {
            uuid: section.section.uuid,
          })
        }
      },
      props: {
        // option: section.section.option.option,
        // cssStyle: section.section.option.cssStyle,
        option: option,
        cssStyle: options.cssStyle,
      }
    }
    
    return (
      // @ts-ignore
      <Pie
        { ..._propsOn }
      ></Pie>
    )
  }
}

// @ts-ignore
PieParse.key = 'PieParse'
// @ts-ignore
PieParse.des = ''
// @ts-ignore
PieParse.icon = 'lx-icon-setting'

export default PieParse
